// variables
$deepBlue:#032f3e;
$sectionHeading:24px;
$offWhite:#f8f9fb;


#main-nav{
    background: $deepBlue;
    //nesting
    ul{
        width: 100%;
        
    }
    li{
        float: left;
        width: 14%;
       
    }
    a{
        color: $offWhite;
        text-decoration: none;
        padding: 16px;
        display: block;
        text-align: center;
    }
}

section h1{
    font-size: $sectionHeading;
    color: $deepBlue;
}